@import url("https://fonts.googleapis.com/css2?family=Nanum+Myeongjo:wght@700&family=Noto+Sans+KR:wght@300&family=Noto+Serif+KR:wght@900&display=swap");
:root {
  --bg-color: #fafbf2;
  --font-color: black;
  --title-font: "Nanum Myeongjo", serif;
  --content-font: "Noto Sans KR", sans-serif;
}

body {
  height: auto;
  display: block;
  /* position: -webkit-sticky; */
  background-color: var(--bg-color);
  text-align: center;
  justify-content: center;
}
.home_title {
  margin-top : 4em;
  margin-bottom : 2em;
}
.home_test {
  font-size: 40px;
  padding : 1em 1em 0em 1em;
}
.home_test_subtitle {
  padding-bottom: 3em;
}
.home_test_go {
  margin-top: 3em;
  margin-bottom : auto;
  margin-left : auto;
  margin-right : auto;
  padding : 1em 2em 1em 2em; 
  background-color: rgb(23, 26, 35);
  color :rgb(218, 219, 223);
  font-size: 20px;
  display : inline-block;
  border-color: rgb(30, 28, 37);
  font-weight :bold;
  font-family : var(--content-font);
}
.main_img {
  margin: 0;
  max-width: 550px;
  width: 100%;
  height: auto;
  font-family: var(--title-font);
  cursor: pointer;
}
.button {
  padding: 1px 6px;
  border-width: 2px;
  border-style: outset;
  background-color: rgb(233, 233, 233);
  border-color: -internal-light-dark(rgb(118, 118, 118), rgb(133, 133, 133));
  border-image: initial;
}
.option-btn {
  font-family: var(--title-font);
  font-weight: bolder;
  font-size: 1.2rem;
  height: 5em;
  width: 20rem;
  margin: 0.5em;
  word-break: keep-all;
  white-space: pre-line;
}
.option-rbtn {
  font-family: var(--title-font);
  font-weight: bolder;
  font-size: 1.2rem;
  height: 11em;
  width: 20rem;
  margin: 0.5em;
  word-break: keep-all;
  white-space: pre-line;
}
.question {
  margin: 3rem 3rem 2rem;
  color: var(--font-color);
  font-family: var(--title-font);
  white-space: pre-line;
  display: block;
  font-size: 2em;
  margin-block-start: 1.67em;
  margin-block-end: 1.67em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
}
