@import url(https://fonts.googleapis.com/css2?family=Nanum+Myeongjo:wght@700&family=Noto+Sans+KR:wght@300&family=Noto+Serif+KR:wght@900&display=swap);
.result_title {
  margin-top: 12px;
  margin-bottom: 12px;
}

.menu_bar a {
  padding-top: 6px;
  padding-bottom: 6px;
  padding-left: 50px;
  padding-right: 50px;
  display: inline-block;
  font-size: 16px;
  border-bottom: 3px;
}

.menu_bar a:hover {
  background-color: rgb(23, 26, 35);
}
.yellow {
  border-bottom: solid;
  border-color: yellow;
}
.sub_menu {
  text-align: left;
  text-align: initial;
  background-color: rgb(23, 26, 35);
  padding: 10px;
  font-size: 12px;
  color: rgb(81, 84, 93);
}
.result_main {
  text-align: start;
  padding-left: 1.5em;
  padding-right: 1.5em;
}

.result_mainstockEng {
  color: rgb(85, 89, 100);
}

.result_have_left {
  width: 13.5em;
  font-size: 13px;
  display: inline-block;
}

.result_have_right {
  width: 13.5em;
  font-size: 13px;
  text-align: right;
  display: inline-block;
}
.result_have_buffer {
  padding-top: 7px;
  padding-bottom: 7px;
}
.result_have_green {
  color: rgb(136, 218, 143);
}
.result_have_red {
  color: rgb(216, 133, 133);
}

.result_personal {
  text-align : left;
  padding-right : 1.5em;
}
.result_personal_font {
  padding-top: 3px;
  padding-bottom: 3px;
  font-size : 12px;
}
.result_compatibility {
  padding-top: 1.5em;
  padding-left: 1.5em;
  padding-right: 1.5em;
}
.result_compatibility_left {
  font-size: 14px;
  text-align: left;
  padding-right: 1em;
  width: 11em;
  height: 30em;
  display: inline-block;
}
.result_compatibility_right {
  font-size: 14px;
  text-align: left;
  padding-left: 1em;
  width: 11em;
  height: 30em;
  display: inline-block;
}
.result_explain {
  font-family: var(--content-font);
  border: 2px;
  border-style: outset;
  border-color: rgb(0, 0, 0);
  border-image: none;
  border-image: initial;
  color: rgb(218, 219, 223);
  width: 25em;
  height: 35em;
  background-color: rgb(29, 31, 43);
  margin-left: auto;
  margin-right: auto;
  font-weight: bold;
}

.result_list {
  text-align: left;
  font-family: var(--content-font);
  font-size: 1.2em;
}

:root {
  --bg-color: #fafbf2;
  --font-color: black;
  --title-font: "Nanum Myeongjo", serif;
  --content-font: "Noto Sans KR", sans-serif;
}

body {
  height: auto;
  display: block;
  /* position: -webkit-sticky; */
  background-color: #fafbf2;
  background-color: var(--bg-color);
  text-align: center;
  justify-content: center;
}
.home_title {
  margin-top : 4em;
  margin-bottom : 2em;
}
.home_test {
  font-size: 40px;
  padding : 1em 1em 0em 1em;
}
.home_test_subtitle {
  padding-bottom: 3em;
}
.home_test_go {
  margin-top: 3em;
  margin-bottom : auto;
  margin-left : auto;
  margin-right : auto;
  padding : 1em 2em 1em 2em; 
  background-color: rgb(23, 26, 35);
  color :rgb(218, 219, 223);
  font-size: 20px;
  display : inline-block;
  border-color: rgb(30, 28, 37);
  font-weight :bold;
  font-family : "Noto Sans KR", sans-serif;
  font-family : var(--content-font);
}
.main_img {
  margin: 0;
  max-width: 550px;
  width: 100%;
  height: auto;
  font-family: "Nanum Myeongjo", serif;
  font-family: var(--title-font);
  cursor: pointer;
}
.button {
  padding: 1px 6px;
  border-width: 2px;
  border-style: outset;
  background-color: rgb(233, 233, 233);
  border-color: -internal-light-dark(rgb(118, 118, 118), rgb(133, 133, 133));
  border-image: none;
  border-image: initial;
}
.option-btn {
  font-family: "Nanum Myeongjo", serif;
  font-family: var(--title-font);
  font-weight: bolder;
  font-size: 1.2rem;
  height: 5em;
  width: 20rem;
  margin: 0.5em;
  word-break: keep-all;
  white-space: pre-line;
}
.option-rbtn {
  font-family: "Nanum Myeongjo", serif;
  font-family: var(--title-font);
  font-weight: bolder;
  font-size: 1.2rem;
  height: 11em;
  width: 20rem;
  margin: 0.5em;
  word-break: keep-all;
  white-space: pre-line;
}
.question {
  margin: 3rem 3rem 2rem;
  color: black;
  color: var(--font-color);
  font-family: "Nanum Myeongjo", serif;
  font-family: var(--title-font);
  white-space: pre-line;
  display: block;
  font-size: 2em;
  -webkit-margin-before: 1.67em;
          margin-block-start: 1.67em;
  -webkit-margin-after: 1.67em;
          margin-block-end: 1.67em;
  -webkit-margin-start: 0px;
          margin-inline-start: 0px;
  -webkit-margin-end: 0px;
          margin-inline-end: 0px;
  font-weight: bold;
}

