.result_title {
  margin-top: 12px;
  margin-bottom: 12px;
}

.menu_bar a {
  padding-top: 6px;
  padding-bottom: 6px;
  padding-left: 50px;
  padding-right: 50px;
  display: inline-block;
  font-size: 16px;
  border-bottom: 3px;
}

.menu_bar a:hover {
  background-color: rgb(23, 26, 35);
}
.yellow {
  border-bottom: solid;
  border-color: yellow;
}
.sub_menu {
  text-align: initial;
  background-color: rgb(23, 26, 35);
  padding: 10px;
  font-size: 12px;
  color: rgb(81, 84, 93);
}
.result_main {
  text-align: start;
  padding-left: 1.5em;
  padding-right: 1.5em;
}

.result_mainstockEng {
  color: rgb(85, 89, 100);
}

.result_have_left {
  width: 13.5em;
  font-size: 13px;
  display: inline-block;
}

.result_have_right {
  width: 13.5em;
  font-size: 13px;
  text-align: right;
  display: inline-block;
}
.result_have_buffer {
  padding-top: 7px;
  padding-bottom: 7px;
}
.result_have_green {
  color: rgb(136, 218, 143);
}
.result_have_red {
  color: rgb(216, 133, 133);
}

.result_personal {
  text-align : left;
  padding-right : 1.5em;
}
.result_personal_font {
  padding-top: 3px;
  padding-bottom: 3px;
  font-size : 12px;
}
.result_compatibility {
  padding-top: 1.5em;
  padding-left: 1.5em;
  padding-right: 1.5em;
}
.result_compatibility_left {
  font-size: 14px;
  text-align: left;
  padding-right: 1em;
  width: 11em;
  height: 30em;
  display: inline-block;
}
.result_compatibility_right {
  font-size: 14px;
  text-align: left;
  padding-left: 1em;
  width: 11em;
  height: 30em;
  display: inline-block;
}
.result_explain {
  font-family: var(--content-font);
  border: 2px;
  border-style: outset;
  border-color: rgb(0, 0, 0);
  border-image: initial;
  color: rgb(218, 219, 223);
  width: 25em;
  height: 35em;
  background-color: rgb(29, 31, 43);
  margin-left: auto;
  margin-right: auto;
  font-weight: bold;
}

.result_list {
  text-align: left;
  font-family: var(--content-font);
  font-size: 1.2em;
}
